/* ========================= 
    Default CSS
   ========================= */
html,
body {
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #848484;
  line-height: 25px;
  letter-spacing: 0.5;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
}
.navbar {
  padding: 0;
}
a:hover,
a:focus {
  text-decoration: none;
}
button:focus,
input:focus {
  outline: 0;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.list-inline li {
  display: inline-block;
  padding-right: 5px;
  padding-left: 5px;
}
h2 {
  font-weight: 500;
  font-size: 36px;
}
h3 {
  color: #626262;
  font-size: 22px;
  font-weight: 500;
}
p {
  line-height: 1.9;
}
.ptb-100 {
  padding: 100px 0;
}
.mrl-50 {
  margin-left: 50px;
}
.diplay-table {
  display: table;
  width: 100%;
  height: 100%;
}
.display-table-cell {
  display: table-cell;
  vertical-align: middle;
}
.section-title h2 {
  margin: 0 0 25px;
  font-weight: 600;
  text-transform: capitalize;
}
.section-title {
  position: relative;
  z-index: 1;
}
.section-title-bg {
  color: #f0f0f0;
  font-size: 100px;
  line-height: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: -1;
  opacity: 0.8;
  font-family: "Monoton", cursive;
  text-transform: uppercase;
}
.button,
.center-wrap button button {
  position: relative;
  display: block;
  background: none center center no-repeat;
  background-size: cover;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  padding: 10px 4px;
  text-align: center;
  max-width: 180px;
  min-width: 170px;
  cursor: pointer;
  border-radius: 4px;
  overflow: hidden;
  -webkit-transition: border 1s cubic-bezier(0.19, 1, 0.22, 1),
    color 0.6s cubic-bezier(0.19, 1, 0.22, 1);
  -webkit-transition: border 1s cubic-bezier(0.19, 1, 0.22, 1),
    color 0.6s cubic-bezier(0.19, 1, 0.22, 1),
    background 5s cubic-bezier(0.19, 1, 0.22, 1);
  transition: border 1s cubic-bezier(0.19, 1, 0.22, 1),
    color 0.6s cubic-bezier(0.19, 1, 0.22, 1),
    background 5s cubic-bezier(0.19, 1, 0.22, 1);
}
.button a,
.center-wrap button {
  color: #fff;
  text-decoration: none;
  font-size: 15px;
  font-weight: 500;
  background: transparent;
  border: transparent;
}
button {
  padding: 0;
}
.button .mask {
  background: #fff;
  background: rgba(255, 255, 255, 0.5);
}
.button .mask {
  position: absolute;
  display: block;
  width: 200px;
  height: 100px;
  -webkit-transform: translate3d(-120%, -50px, 0) rotate3d(0, 0, 1, 45deg);
  transform: translate3d(-120%, -50px, 0) rotate3d(0, 0, 1, 45deg);
  -webkit-transition: all 1.1s cubic-bezier(0.19, 1, 0.22, 1);
  transition: all 1.1s cubic-bezier(0.19, 1, 0.22, 1);
}
.button:hover {
  border-color: #fff;
}
.button:hover a {
  color: #fff;
}
.button:hover .mask {
  background: #fff;
  -webkit-transform: translate3d(120%, -100px, 0) rotate3d(0, 0, 1, 90deg);
  transform: translate3d(120%, -100px, 0) rotate3d(0, 0, 1, 90deg);
}
.btn-a {
  color: #fff;
  text-decoration: none;
  font-size: 15px;
  font-weight: 500;
  background: transparent;
  border: transparent;
}
.btn-a:hover {
  color: #fff;
}
.plan .btn-a {
  color: #272727;
}
.featured .btn-a {
  color: #fff !important;
}
.plan .button {
  background-color: transparent;
  margin: auto;
  margin-top: 40px;
}
.plan.featured .button {
  margin: auto;
  margin-top: 40px;
}
.plan.featured .button a {
  color: #fff;
}
.all-post .button {
  margin: 50px auto 0;
}
.all-post .button a {
  color: #fff;
}
.all-post .button a:hover {
  color: #fff;
}
